.card {
  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    padding: 32px 16px 16px;
    background: #fff;
    border: 1px solid #f3f3f3;
    border-radius: 5px;
    box-sizing: border-box;
  }

  &__photo {
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 4px;
  }

  &__product-code {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $secondary;
    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    &-text {
      color: $main-accent;
      text-align: right;
      font-size: 10px;
      font-weight: 400;
      line-height: 14px;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-text {
      color: $secondary;
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
    }

    &-amount {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__button {
    box-sizing: border-box;
    text-decoration: none;
    width: 166px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    border: 1px solid $blue-accent ;
    border-radius: 5px;
    background-color: $blue-accent;
    color: #fff;
    &:hover {
      color: $blue-accent;
      background-color: #fff;
    }
  }
}
